<template>
  <div>
    <a-row :gutter="24" type="flex">
      <a-col :span="24" class="mb-24">
        <x-table
          entityName="indicator"
          :data="indicator"
          :columns="columns"
          :actions="['create', 'read', 'edit', 'delete']"
          @delete="deleteItem"
        >
        </x-table>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import { INDICATOR } from '@/helpers/constants'
const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
  },
  {
    title: 'Название',
    dataIndex: 'name',
    filterable: true,
  },
  {
    title: 'Максимальное количество достижений',
    dataIndex: 'max_count_achievement',
    filterable: true,
  },
  {
    title: 'Количество баллов за достижение',
    dataIndex: 'count_score_per_achievement',
    filterable: true,
  },
  {
    title: 'Номинация',
    dataIndex: 'nomination.name',
    filterable: true,
  },
]

export default {
  data() {
    return {
      columns,
    }
  },
  async mounted() {
    await this.getIndicator()
  },
  computed: {
    indicator() {
      return this.$store.state.app.indicator
    },
  },
  methods: {
    async getIndicator() {
      try {
        await this.$store.dispatch('app/getEntities', {
          entityName: INDICATOR,
        })
      } catch (e) {
        console.log(e)
      }
    },
    async deleteItem(id) {
      this.loading = true
      try {
        await this.$store.dispatch('app/deleteItem', {
          entityName: INDICATOR,
          id
        })
        this.$notification.success({message: 'Успешно удалено'})
      } catch(e) {
        console.log(e)
        this.$errors.showOnce(e.response.data)
      } finally {
        this.loading = false
      }
    }
  },
}
</script>

